@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
.sidebar {
    display: flex;
    flex-direction: column;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: rgb(54, 9, 73);
    z-index: 1;
}

.sb-item {
    display: flex;
    width: 100%;
    height: 2.5em;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: whitesmoke;
}

.sb-item>span {
    font-size: 1em;
    padding-left: 0.2em;
}

.sb-item:active>.sb-icon {
    transform: translateY(5px);
}

.sb-item:active>span {
    transform: translateY(5px);
}

.sb-item:hover {
    background-color: rgb(231, 73, 99);
}

.sb-active {
    background-color: rgb(231, 73, 99);
}

.sb-icon {
    width: 1em;
    height: 1em;
    color: whitesmoke;
}

/*Modern Mobile*/
@media only screen and (min-width: 520px) {
    .sidebar {
        position: absolute;
        width: 100vw;
        justify-content: space-between;
        height: 5.5em;
        transition: height 0.5s;
        flex-direction: row;
    }

    .sidebar>div {
        display: flex;
    }

    .sb-item {
        width: 4.5em;
        flex-direction: column;
        justify-content: start;
        height: 100%;
    }

    .sb-item>span {
        font-size: 1.5em;
        transform: rotate(-270deg);
        opacity: 0;
        width: 0;
        overflow: hidden;
        transition: opacity 0.1s ease-in 0.05s;
    }

    .sb-item:active>.sb-icon {
        transform: translateY(5px);
    }

    .sb-item:active>span {
        transform: rotate(-270deg) translateX(5px);
    }

    .sidebar:hover {
        height: 15em;
    }

    .sidebar:hover span {
        opacity: 1;
        overflow: visible;
        overflow: initial;
    }

    .sb-icon {
        min-width: 2.5em;
        min-height: 2.5em;
        margin-top: 1.5em;
    }
}

/*Desktop view*/
@media only screen and (min-width: 1024px) and (min-height: 500px) {
    .sidebar {
        position: fixed;
        width: 5.5em;
        transition: width 0.5s;
        height: 100vh;
        flex-direction: column;
    }

    .sb-item>span {
        transform: rotate(0deg);
    }

    .sidebar>div {
        flex-direction: column;
    }

    .sb-item {
        flex-direction: row;
        width: 100%;
        height: 4.5em;
        justify-content: start;
    }

    .sb-item:active>span {
        transform: translateY(5px);
    }

    .sidebar:hover {
        width: 15em;
        height: 100vh;
    }

    .sb-icon {
        margin-left: 1.5em;
        margin-top: 0;
    }
}
.ev .interface>label {
    font-size: 1.2em;
    font-weight: bold;
    display: flex;
    flex-direction: column;
}

.ev {
    display: flex;
    flex-direction: column;
}

.ev .interface {
    display: flex;
    flex-direction: column;
}

.ev .inputs>label {
    margin-top: 20px;
}

.ev .outputs>label {
    margin-top: 50px;
}

.ev .symbol {
    display: flex;
    position: relative;
    align-items: center;
}

.ev .symbol>span {
    font-weight: normal;
    position: absolute;
    color: black;
    font-size: 1.5em;
    color: rgb(125, 50, 155);
    pointer-events: none;
}

.ev .currency>span {
    left: 0.55em;
}

.ev .odd>span {
    right: 0.55em;
}

.ev .currency>input {
    padding-left: 1.6em;
}

.ev .odd>input {
    padding-left: 0.5em;
    padding-right: 1.6em;
}

.ev .interface input {
    height: 2em;
    font-size: 1.5em;
    flex: 1 1;
    max-width: 100%;
    border-radius: 5px;
    border: 2px solid rgb(54, 9, 73);
    color: rgb(125, 50, 155);
    background-color: whitesmoke;
}

.ev .interface input:focus {
    outline: rgb(231, 73, 99) auto 2px;
}

.ev button {
    height: 2em;
    color: whitesmoke;
    font-size: 1.5em;
    background-color: rgb(54, 9, 73);
    border: 2px solid rgb(54, 9, 73);
    border-radius: 5px;
    margin-top: 5px;
    cursor: pointer;
}

.ev button:focus {
    outline: rgb(231, 73, 99) auto 2px;
}

.ev button:active {
    background-color: rgb(231, 73, 99);
    transform: translateY(5px);
}

/*Modern Mobile*/
@media only screen and (min-width: 520px) {
    .ev .outputs {
        flex-direction: row;
        align-items: flex-end;
        justify-content: center;
    }

    .ev .inputs {
        align-items: flex-start;
    }

    .ev .outputs>label {
        margin-right: 10px;
    }

    .ev .interface input {
        max-width: 400px;
    }

    .ev button {
        flex: 1 1;
        margin-left: 10px;
        max-width: 200px;
    }
}

/*Desktop view*/
@media only screen and (min-width: 1024px) and (min-height: 500px) {
    .ev .outputs {
        justify-content: start;
    }
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: 'Muli', sans-serif;
    -webkit-tap-highlight-color: transparent;
}

#root {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.content {
    padding: 20px;
    background-color: rgb(99, 0, 138);
    flex: 1 1;
    color: whitesmoke;
    min-height: 100vh;
}

.content>h1 {
    line-height: 1.5em;
}

.content>strong {
    margin-bottom: 1em;
}

.hidden {
    visibility: hidden;
}

::-webkit-scrollbar {
    width: 10px;
    background-color: rgb(99, 0, 138);
}

::-webkit-scrollbar-track {
    background: whitesmoke;
    border-radius: 10px;
    box-shadow: inset 0 0 5px grey;
}

::-webkit-scrollbar-thumb {
    background: rgb(231, 73, 99);
    border-radius: 10px;
}

::selection {
    color: rgb(231, 73, 99);
    background-color: rgb(99, 0, 138);
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}

/*Modern Mobile*/
@media only screen and (min-width: 520px) {
    .content {
        margin-top: 5.5em;
    }
}

/*Desktop view*/
@media only screen and (min-width: 1024px) and (min-height: 500px) {
    .content {
        margin-top: 0;
        margin-left: 5.5em;
    }
}
